* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  user-select: none;
}

:root {
  --bgcolor: hsl(0, 9%, 80%);
  --calcfont: hsl(0, 20%, 65%);
  --calc1: hsl(0, 20%, 55%);
  --calc2: hsl(0, 30%, 80%);
  --calcbg: hsl(120, 36%, 85%);
  --calcbg2: hsl(120, 38%, 80%);
  --textcolor: hsl(120, 25%, 10%);
}

#main-app {
  background-color: var(--bgcolor);

  width: 100vw;
  height: 100vh;
  position: fixed;
  display: flex;
  align-items: center;
}

#calculator {
  background-image: url("https://i.imgur.com/OxSTyqM.png");
  background-size: 60%;
  background-position-x: -40px;
  background-position-y: 70px;
  background-repeat: repeat;
  width: 500px;
  height: 650px;
  border: 6px groove var(--calc1);
  border-radius: 28.5px;
  margin: 0 auto;
}

.calc-div {
  width: 100%;
}

#display-div {
  background: var(--calc2);
  border-radius: 28.5px 28.5px 0 0;
  height: 20%;
  padding: 10px 17.5px;
  width: 100%;
}

#disptext {
  font-size: 10.5px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: hsla(0, 0%, 30%, 1);
  position: relative;
  bottom: 3px;
  border: 5px;
  height: 10%;
  width: 100%;
  margin-bottom: 1%;
  display: flex;
  justify-content: center;
  gap: 210px;
}
#author {
  font-style: italic;
}

#disp1 {
  color: var(--textcolor);
  background: var(--calcbg);
  border: 5px;
  height: 30%;
  width: 100%;
  display: flex;
  justify-content: right;
  align-items: center;
  text-align: right;
  padding: 10px;
  font-size: 100%;
}

#display {
  color: var(--textcolor);
  background: var(--calcbg2);
  border: 3px;
  height: 55%;
  width: 100%;
  display: flex;
  justify-content: right;
  align-items: center;
  text-align: right;
  padding: 10px;
  font-size: 250%;
}

#input-div {
  height: 80%;
  border-top: 0.5px solid black;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 1.05%;
  padding: 8.5%;
}
.btn {
  border: 2px outset grey;
  border-radius: 9px;
  box-shadow: 0 0 6px 0 rgb(138, 138, 138);
  height: auto;
  width: auto;
}

.btn:hover {
  filter: brightness(1.1);
}

.btn:active {
  filter: brightness(0.9);
  border: 3px inset grey;
  box-shadow: none;
}

#clear {
  grid-column: 1 / 3;
  grid-row: 1;
}

#zero {
  grid-column: 1/3;
  grid-row: 5;
}

#decimal {
  grid-column: 3;
  grid-row: 5;
}

#equals {
  grid-column: 4;
  grid-row: 4/6;
}

.btn-op {
  background: url("https://i.pinimg.com/736x/a6/f6/e9/a6f6e9f830162f4613f6aeaf9a693c9d.jpg");
  background-size: 80%;
}

.button-text-op {
  font-family: "Segoe UI", sans-serif;
  color: var(--calc1);
  font-size: 225%;
  font-weight: 600;
}

.button-text {
  font-family: "Segoe UI", sans-serif;
  color: var(--calcfont);
  font-size: 200%;
}

#ac {
  background-color: hsl(0, 50%, 80%);
}

#creator-tag {
  z-index: -2;
  position: fixed;
  right: 10px;
  bottom: 10px;
  max-width: 30%;
  font-size: 10px;
  color: rgba(0, 0, 0, 0.548);
}
#creator-tag > * {
  z-index: -2;
}
#creator-tag > #codeAuthor {
  color: black;
}

.bold {
  font-weight: bold;
}
